.App {
  text-align: center;
  display: block;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}


h1 {
  font-family: 'Bigelow Rules';
  font-size: 72px;
  color: #09d3ac;
}

body {
  padding: 30px 0;

   @media screen and (max-width: 800px){
    padding: 1px 1px;
  }

}

